@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@300;400;700;800&display=swap'); 

// Base Line-Height
$base-line-height: 120%;

// Font Family
$base-font-family: 'Manrope', sans-serif;

// Font Weight
$font-weight-light: 300;
$font-weight-regular: 400;
$font-weight-bold: 700;
$font-weight-extra-bold: 800;

// Letter Spacing
$letter-spacing-small-text: 3%;
$letter-spacing-caption: 2%;
$letter-spacing-body: 1%;
$letter-spacing-header-3: -3.5%;
$letter-spacing-header-2: -4%;
$letter-spacing-header-1: -4.5%;
$letter-spacing-display-3: -5%;
$letter-spacing-display-2: -5.5%;
$letter-spacing-display-1: -6%;

// Font Size Mobile
$mobile-font-size-small-text: 12px;
$mobile-font-size-caption: 14px;
$mobile-font-size-body: 16px;
$mobile-font-size-header-3: 19px;
$mobile-font-size-header-2: 22px;
$mobile-font-size-header-1: 26px;
$mobile-font-size-display-3: 30px;
$mobile-font-size-display-2: 35px;
$mobile-font-size-display-1: 41px;

// Font Size Tablet-to-Web
$tablet-to-web-font-size-small-text: 10px;
$tablet-to-web-font-size-caption: 12px;
$tablet-to-web-font-size-body: 16px;
$tablet-to-web-font-size-header-3: 20px;
$tablet-to-web-font-size-header-2: 26px;
$tablet-to-web-font-size-header-1: 34px;
$tablet-to-web-font-size-display-3: 43px;
$tablet-to-web-font-size-display-2: 55px;
$tablet-to-web-font-size-display-1: 70px;

// Mixins

@mixin small-text{
    line-height: $base-line-height;
    font-family: $base-font-family;
    font-weight: $font-weight-light;
    letter-spacing: $letter-spacing-small-text;
}

@mixin caption{
    line-height: $base-line-height;
    font-family: $base-font-family;
    font-weight: $font-weight-light;
    letter-spacing: $letter-spacing-caption;
}

@mixin body{
    line-height: $base-line-height;
    font-family: $base-font-family;
    font-weight: $font-weight-regular;
    letter-spacing: $letter-spacing-body;
}

@mixin header-3{
    line-height: $base-line-height;
    font-family: $base-font-family;
    font-weight: $font-weight-bold;
    letter-spacing: $letter-spacing-header-3;
}

@mixin header-2{
    line-height: $base-line-height;
    font-family: $base-font-family;
    font-weight: $font-weight-bold;
    letter-spacing: $letter-spacing-header-2;
}

@mixin header-1{
    line-height: $base-line-height;
    font-family: $base-font-family;
    font-weight: $font-weight-bold;
    letter-spacing: $letter-spacing-header-1;
}

@mixin display-3{
    line-height: $base-line-height;
    font-family: $base-font-family;
    font-weight: $font-weight-extra-bold;
    letter-spacing: $letter-spacing-display-3;
}

@mixin display-2{
    line-height: $base-line-height;
    font-family: $base-font-family;
    font-weight: $font-weight-extra-bold;
    letter-spacing: $letter-spacing-display-2;
}

@mixin display-1{
    line-height: $base-line-height;
    font-family: $base-font-family;
    font-weight: $font-weight-extra-bold;
    letter-spacing: $letter-spacing-display-1;
}
