@use '../foundation/foundation-compiler' as f;

footer{
    background-color: f.$primary-300;
    height: 100%;
    padding: 3rem 2rem;
    @include f.LG{
        padding: 3.5rem 0;
    }
}

.footer-left-column a{
    color: f.$text-400;
    font-size: f.$mobile-font-size-body;
    margin: .5rem 0;
    text-decoration: none;
    transition: color 800ms ease;
    @include f.caption;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-body;
    }
}

.footer-left-column a:hover{
    color: f.$text-50;
}

.footer-right-column{
    margin-top: 1rem;
    gap: 1rem;
    @include f.LG{
        gap: 1.5rem;
        margin-top: 0;
    }
}

.footer-right-column .bi{
    color: f.$text-500;
    font-size: 1.5rem;
    transition: color 800ms ease;
    @include f.LG{
        font-size: 2rem;
    }
}

.footer-right-column .bi:hover{
    color: f.$text-50;
}