@use '../foundation/foundation-compiler' as f;

.hero-section{
    background-color: f.$secondary-50;
    height: 120%;
    padding: 5rem 2rem;
    @include f.LG{
        padding: 12rem 0;
    }
}

.hero-column{
    margin: 8.5rem 0;
}

.hero-left-column{
    margin-top: 5rem;
    @include f.MD{
        margin-top: 0;
    }
}

.hero-left-column h1{
    color: f.$text-500;
    font-size: f.$mobile-font-size-display-1;
    margin-bottom: 3rem;
    @include f.display-1;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-display-1;
    }
}

.hero-left-column h2{
    color: f.$text-300;
    font-size: f.$mobile-font-size-header-1;
    max-width: 615px;
    @include f.header-1;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-header-1;
    }
}

