@use '../foundation/foundation-compiler' as f;

.about-section{
    background-color: f.$secondary-50;
    height: 120%;
    padding: 5rem 2rem;
    @include f.LG{
        padding: 12rem 0;
    }
}

.about-left-column h1{
    color: f.$text-500;
    font-size: f.$mobile-font-size-display-3;
    margin-bottom: 2rem;
    @include f.display-3;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-display-3;
        margin-bottom: 3rem;
    }
}

.about-left-column p{
    color: f.$text-400;
    font-size: f.$mobile-font-size-header-2;
    margin-bottom: 1rem;
    @include f.header-2;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-header-2;
        margin-bottom: 2.5rem;
    }
}

.about-right-column{
    background-color: f.$primary-300;
    border-radius: 8px;
    padding: 2.5rem 1rem;
}

.about-right-column .bi{
    color: f.$text-50;
    font-size: 2rem;
}

.about-right-inner-top-column{
    margin-bottom: 1.5rem;
    @include f.LG{
        margin-bottom: 2.5rem;
    }
}

.about-right-inner-top-column .bi-compass{
    margin-right: .5rem;
}

.about-right-inner-top-column h1{
    color: f.$text-50;
    font-size: f.$mobile-font-size-header-1;
    @include f.header-1;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-header-1;
    }
}

.about-right-inner-top-column p{
    color: f.$text-50;
    font-size: f.$mobile-font-size-header-3;
    @include f.header-3;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-header-3;
    }
}

.about-right-inner-bottom-column .bi-envelope{
    margin-right: .5rem;
}

.about-right-inner-bottom-column h1{
    color: f.$text-50;
    font-size: f.$mobile-font-size-header-1;
    @include f.header-1;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-header-1;
    }
}

.about-right-inner-bottom-column a{
    color: f.$text-50;
    font-size: f.$mobile-font-size-header-3;
    text-decoration: none;
    @include f.header-3;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-header-3;
    }
}

.about-right-inner-bottom-column a:hover{
    text-decoration: underline;
}