@use '../foundation/foundation-compiler' as f;

nav{
    background-color: f.$primary-300;
}

nav .container{
    padding: 1rem 1.5rem;
    @include f.LG{
        padding: 1rem 0;
    }
}

.navbar-brand img{
    max-width: 150px;
}

.navbar-nav{
    margin-top: 1rem;
    @include f.LG{
        margin-top: 0;
    }
}

.nav-item{
    padding: 1rem 0;
    @include f.LG{
        padding: 0 1.5rem;
    }
}

.nav-link{
    font-size: f.$mobile-font-size-header-3;
    color: f.$text-50;
    transition: color 800ms ease-out;
    @include f.header-3;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-header-3;
    }
}

.nav-link:hover{
    color: f.$text-500;
}