@use '../foundation/foundation-compiler' as f;

.news-section{
    background-color: f.$secondary-50;
    height: 100%;
    padding: 5rem 2rem;
    @include f.LG{
        padding: 12rem 0;
    }
}

.news-top-column{
    margin-bottom: 4rem;
    @include f.LG{
        margin-bottom: 7rem;
    }
}

.news-top-column h1{
    color: f.$text-500;
    font-size: f.$mobile-font-size-display-1;
    @include f.display-1;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-display-1;
    }
}

.card .card-body{
    background-color: f.$secondary-300;
    padding: 2.5rem 2rem 5rem;
}

.card-title{
    color: f.$text-500;
    font-size: f.$mobile-font-size-header-2;
    margin-bottom: 1rem;
    @include f.header-2;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-header-2;
    }
}

.card-text{
    color: f.$text-400;
    font-size: f.$mobile-font-size-body;
    margin-bottom: 2rem;
    @include f.body;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-body;
    }
}

.card-button{
    background-color: f.$primary-500;
    color: f.$text-50;
    font-size: f.$mobile-font-size-body;
    padding: 1rem 2rem;
    transition: color 800ms ease;
    @include f.body;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-body;
    }
}

.card-button:hover{
    background-color: f.$primary-50;
    color: f.$text-500;
}

.news-bottom-column{
    margin-top: 3.5rem;
    @include f.LG{
        margin-top: 7rem;
    }
}

.news-bottom-column button{
    background-color: f.$primary-500;
    color: f.$text-50;
    font-size: f.$mobile-font-size-header-2;
    padding: 1rem 5rem;
    @include f.header-2;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-header-2;
    }
}

.news-bottom-column button:hover{
    background-color: f.$primary-300;
    color: f.$text-500;
}