@use '../foundation/foundation-compiler' as f;

.service-section{
    background-color: f.$primary-300;
    height: 100%;
    padding: 5rem 2rem;
    @include f.LG{
        padding: 12rem 0;
    }
}

.service-left-inner-top-column{
    margin-bottom: 4rem;
}

.service-left-inner-top-column h1{
    color: f.$text-50;
    font-size: f.$mobile-font-size-display-1;
    margin-bottom: 2rem;
    @include f.display-1;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-display-1;
    }
}

.service-left-inner-top-column p{
    color: f.$text-50;
    font-size: f.$mobile-font-size-header-2;
    max-width: 588px;
    @include f.header-2;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-header-2;
    } 
}

.service-left-inner-middle-column{
    margin-bottom: 1.5rem ;
}

.service-left-inner-middle-column h1, li{
    color: f.$text-50;
    font-size: f.$mobile-font-size-header-2;
    @include f.header-2;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-header-2;
    }
}

.service-left-inner-middle-column h1{
    margin-bottom: 1rem;
}

.service-left-inner-middle-column li{
    padding: .5rem 0;
}

.service-left-inner-bottom-column button{
    background-color: f.$primary-500;
    color: f.$text-50;
    font-size: f.$mobile-font-size-header-3;
    padding: 1rem 0;
    transition: color 800ms ease;
    @include f.header-3;
    @include f.LG{
        font-size: f.$tablet-to-web-font-size-header-3;
    }
}

.service-left-inner-bottom-column button:hover{
    background-color: f.$primary-50;
    color: f.$text-500;
}